import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    user: {
      id: 0,
      nickname: 'Guest',
      username: ''
    },
    genre: {
      'umigame': '',
      'tobira': '20',
      'kameo': '',
      'other': ''
    },
    site: {
      'latethink': {name: '', showUrl: 'http://sui-hei.net/mondai/show/'},
      'cindy': {name: 'Cindy', showUrl: 'https://www.cindythink.com/puzzle/show/'},
      'R': {name: 'Open R', showUrl: 'http://openumigame.sakura.ne.jp/openumi/mondai/show/'}
    }
  },
  mutations: {
    setUser (state, value) {
      state.user = value
    }
  },
  getters: {
    user (state) { return state.user },
    genre (state) { return state.genre },
    site (state) { return state.site }
  }
})
